fun generateCookie(name: String, value: String) {
    val date = Date()
    val ex = "expires=" + Date(date.getTime() + (24 * 60 * 60 * 1000)).toUTCString()
    document.cookie = name + "=" + value + ";" + ex + ";path=/"
}


fun accessCookie(name: String): String {
    val value = document.cookie.split(';').find { row -> row.startsWith(name) }
    return if (value != null) value.split('=')[1]
    else ""
}

object Constants {
    //val mainRectRatio = 0.8 // width / height
    var appWidth = 300
    val dictionary = Dictionary()
    val letterLimit = 5
    val attempts = 6
    val backgroundColor = "#EDEAEA"
    val textColor = "#110A01"
    val accordeonLimit = 0.6
}

fun bg(color: String) {
    if (color != "") {
        document.bgColor = color
    }
}

fun challenge(game: Game) {
    println(game.createChallenge())
}

fun Node.sayHello(game: Game) {
    val topPart = TopPart()
    val keyboard = Keyboard()
    val midBanner = MidBanner().also {
        it.challenge = { challenge(game) }
    }
    val adBanner = AdBanner()
    val ultra = topPart.heightToWidth + keyboard.heightToWidth + midBanner.heightToWidth + adBanner.heightToWidth
    //val heightRef = ultra + (game.attempts.toDouble()/game.letters.toDouble())*Constants.accordeonLimit
    val padHeight = (window.innerHeight-20).toDouble()
    val padWidth = (window.innerWidth).toDouble()
    val gridRate = (game.letters.toDouble()/game.attempts.toDouble())
    val squeeze = (padHeight-ultra* padWidth)/padWidth*gridRate

    val newSqueeze: Double
    if(squeeze>1.0){ //tall
        Constants.appWidth = padWidth.toInt()
        newSqueeze = 1.0
    }else if(squeeze<Constants.accordeonLimit){ // fat
        Constants.appWidth = (padHeight/(ultra + Constants.accordeonLimit/gridRate)).toInt()
        newSqueeze = Constants.accordeonLimit
    }else{ // fish meat
        Constants.appWidth = padWidth.toInt()
        newSqueeze = squeeze
    }



    /*
    val adjustedAccordeon = 0.0

    if((window.innerHeight-20)/window.innerWidth>heightRef){// taller screen
        Constants.appWidth = (window.innerWidth)

    } else{ // fatter screen
        Constants.appWidth = ((window.innerHeight-20).toDouble()/heightRef).toInt()
    }

     */
    val gridHeight = Constants.appWidth/gridRate*newSqueeze
    val gridPart = GridPart(
        Constants.appWidth.toDouble(),
        gridHeight,
        Constants.attempts,
        Constants.letterLimit
    )

    keyboard.letterPressed = { c ->
        if (!game.gameSolved && !game.gameLost) {
            game.addLetter(c)
            gridPart.writeLetter(game.recordedWord)
        }

    }

    keyboard.erasePressed = {
        if (!game.gameSolved && !game.gameLost) {
            game.removeLetter()
            gridPart.writeLetter(game.recordedWord)
        }
    }

    keyboard.enterPressed = {
        if (!game.gameSolved && !game.gameLost) {
            game.checkWord()
            gridPart.paintRow(game.guessDatabase)
            keyboard.paintKeys(game.guessDatabase,game.letters)
            if (game.gameSolved) {
                window.alert("Cevap bulundu, yürü be.");
                // TODO game solved
            }
            if(game.gameLost){
                window.alert("cevap ${game.gameWord}. Bir dahakine artık.")
            }
            gridPart.activeRow = game.activeRow
            gridPart.moveTriangle()
        }
    }

    topPart.replayFunction = {
        game.startRound()
        keyboard.resetKeys()
        gridPart.clear()
        gridPart.activeRow = game.activeRow
        gridPart.moveTriangle()
    }


    append {
        div {
            id = "main"
        }.also {
            it.style.let { style ->
                style.color = Constants.textColor
                style.margin = "auto"
                style.width = toPx(Constants.appWidth)
                style.height = toPx(window.innerHeight - 20)
                style.backgroundColor = Constants.backgroundColor
                style.position = "relative"
            }

            it.append {
                div {
                    id = "topPart"
                }.also {
                    it.append(topPart.generate(Constants.appWidth))
                }



                div {
                    id = "grid"
                }.also {
                    it.append(gridPart.createGrid())
                }


                div {
                    id = "commercial"
                    style = """
                        margin: auto;
                        width: 100%;
                        position: absolute;
                        bottom: ${toPx(keyboard.expectedHeight(Constants.appWidth) + midBanner.expectedHeight(Constants.appWidth))};
                        left: 0;
                        background-color: #44FF44;
                    """
                }.also {
                    it.append(adBanner.generate(Constants.appWidth))
                }


                div {
                    id = "midBannerBox"
                    style = """
                        width: 100%;
                        position: absolute;
                        bottom: ${toPx(keyboard.expectedHeight(Constants.appWidth))};
                        left: 0px;
                    """
                }.also {
                    it.append(midBanner.generate(Constants.appWidth))
                }




                div {
                    id = "keyboardBox"
                    style = """
                        width: 100%;
                        position: absolute;
                        bottom: 0px;
                        left: 0px;                        
                    """
                }.also {
                    it.append(keyboard.generate(Constants.appWidth))
                }
            }
        }
    }
}


fun toPx(v: Double): String {
    return v.toInt().toString() + "px"
}

fun toPx(v: Int): String {
    return v.toString() + "px"
}